  // COSTA RICA
  suma(campo1: string, value: number): number {
    if (!this.formulario) {
      return 0;
    }
    if (this.pais === 'PA') {
      const total =
        (parseFloat(this.formulario.get(campo1).value) || 0) + value;
      return Number(total.toFixed(2));
    }
    return (parseInt(this.formulario.get(campo1).value, 10) || 0) + value;
  }

