  // #region EL SALVADOR

  requiredActividadLaboral(ng: boolean = true): (control: FormGroup) => boolean {
    return (control: FormGroup) => {
      if (!control.parent) {
        return ng ? true : false;
      }
      const v = !(control.parent.get('actividadLaboral').value === '0001' ||
        control.parent.get('actividadLaboral').value === '0002' ||
        control.parent.get('actividadLaboral').value === '0003');
      return ng ? v : !v;
    };
  }

  requiredActividadLaboralNivel2(): (control: FormGroup) => boolean {
    return (control: FormGroup) => {
      if (!control.parent) {
        return true;
      }
      return !(control.parent.parent.get('actividadLaboral').value === '0001' ||
        control.parent.parent.get('actividadLaboral').value === '0002' ||
        control.parent.parent.get('actividadLaboral').value === '0003');
    };
  }

  requiredActividadLaboralNivel3(ng: boolean = true): (control: FormGroup) => boolean {
    return (control: FormGroup) => {
      if (!control.parent) {
        return ng ? true : false;
      }
      const v = !((control.parent.get('actividadLaboral').value === '0001' ||
        control.parent.get('actividadLaboral').value === '0002' ||
        control.parent.get('actividadLaboral').value === '0003') &&
        control.parent.get('pais').value === 'SV');
      return ng ? v : !v;
    };
  }

  setRulesSV(): void {
    this.catalogoData = {
      SV: {
        ACTIVIDAD_LABORAL: [],
        PROFESIONES: [],
        CARGO: [],
        // CONTRATO: [],
        PAISES: [],
        catDepartamentos: [],
        catCiudades: []
      }
    };
    this.erroresSV = {
      actividadLaboral: [],
      profesion: [],
      nombreEmpresa: [],
      cargoEmpresa: [],
      // tipoContrato: [],
      dia: [],
      mes: [],
      anio: [],
      fechaIngreso: [],
      pais: [],
      departamento: [],
      municipio: [],
      direccion: [],
      telefono: []
    };
    this.getCatalogo('ACTIVIDAD_LABORAL');
    this.getCatalogo('PROFESIONES');
    this.getCatalogo('CARGO');
    // this.getCatalogo('CONTRATO');
    this.getCatalogo('PAISES');
    this.getCatalogoGeoV2(zonaGeograficaNivel1);
    this.setRules({
      actividadLaboral: ['', [
        this.rules.validationForSelect(() => this.catalogoData.SV.ACTIVIDAD_LABORAL, (control: FormGroup) => false),
        this.rules.resetMyCallbackValidation([
          'profesion',
          'nombreEmpresa',
          'cargoEmpresa',
          // 'tipoContrato',
          'fechaIngreso',
          'pais',
          'departamento',
          'municipio',
          'direccion',
          'telefono'
        ])
      ]],
      profesion: ['', [
        this.rules.validationForSelect(() => this.catalogoData.SV.PROFESIONES, this.requiredActividadLaboral())
      ]],
      nombreEmpresa: ['', [
        this.rules.validationForRequired(this.requiredActividadLaboral())
      ]],
      cargoEmpresa: ['', [
        this.rules.validationForRequired(this.requiredActividadLaboral())
      ]],
      // tipoContrato: ['', [
      //   this.rules.validationForSelect(() => this.catalogoData.SV.CONTRATO, this.requiredActividadLaboral())
      // ]],
      fechaIngreso: this.formBuilder.group({
        dia: ['', [
          this.rules.validationForRequired(this.requiredActividadLaboralNivel2()),
          this.rules.validationForMinLength(2)
        ]],
        mes: ['', [
          this.rules.validationForRequired(this.requiredActividadLaboralNivel2()),
          this.rules.validationForMinLength(2)
        ]],
        anio: ['', [
          this.rules.validationForRequired(this.requiredActividadLaboralNivel2()),
          this.rules.validationForMinLength(4)
        ]]
      }, {
        validators: [
          this.rules.validationForMaxDateGroup(0, 'day', this.requiredActividadLaboral(false))
        ]
      }),
      pais: ['', [
        this.rules.validationForSelect(() => this.catalogoData.SV.PAISES, this.requiredActividadLaboral()),
        this.rules.resetMyCallbackValidation([
          'departamento',
          'municipio'
        ])
      ]],
      departamento: ['', [
        this.rules.validationForSelect(() => this.catalogoData.SV.catDepartamentos, this.requiredActividadLaboralNivel3())
      ]],
      municipio: ['', [
        this.rules.validationForSelect(() => this.catalogoData.SV.catCiudades, this.requiredActividadLaboralNivel3())
      ]],
      direccion: ['', [
        this.rules.validationForRequired(this.requiredActividadLaboral())
      ]],
      telefono: ['', [
        this.rules.validationForRequired(this.requiredActividadLaboral()),
        this.rules.validationForMinLength(8, this.requiredActividadLaboral())
      ]]
    });
    this.erroresSV = new ErroresSV().get(this.rules);
  }

  filterDepartamento(valor) {
    return (item) => item.value_pais === valor;
  }

  filterDepartamentoMunicipio(valor1, valor2) {
    return (item) => item.value_pais === valor1 && item.value_departamento === valor2;
  }
