  get getErroresnumeroIdentificacionDeQuienDepende() {
    return [
      { type: this.rules.errorTypeRequired, label: 'Información Requerida.'},
      { type: this.rules.errorTypeMinLength, label: `No se permiten menos de ${this.ccMaxLength} caracteres.`}
    ];
  }

  validacionFechaDesdeHasta(): ValidatorFn {
    return (control: FormGroup): ValidationErrors | null => {
      const informacionRequerida = control.get('actividadComercial').value === this.AMA_DE_CASA_HN ||
        control.get('actividadComercial').value === this.ESTUDIANTE_HN ||
        control.get('actividadComercial').value === this.DESEMPLEADO ||
        control.get('tieneCargoPublicoEnGorbierno').value === false ||
        control.get('esElActual').value === true;
      if ( informacionRequerida ) {
        return null;
      }
      const desde = control.get('desde').value;
      const hasta = control.get('hasta').value;
      const result = new Date(desde.anio, desde.mes, desde.dia) < new Date(hasta.anio, hasta.mes, hasta.dia);
      if ( result ) {
        return null;
      }
      return {
        desdeHasta: true
      };
    };
  }

