  filterByActividadComercial(): (item) => boolean {
    return (item) => item.actividad_comercial === this.formulario.get('actividadComercial').value;
  }

  getCatalogo(KEY: string): void {
    this.catalogoData.SV[KEY] = [];
    if (this.pais === 'PA' && KEY === 'PROFESIONES') {
      const params = this.params([PAIS, MODULO, CANAL, LENGUAJE]);
      params.canal = 1;
      this.catalogo.data(KEY, params).subscribe(response => {
        this.catalogoData.SV[KEY] = response ?
          response.map((p: any) => Object.assign({ value: p.codigo || p.codigoCiiu, label: p.nombre || p.descripcionCiiu }, p))
          : [];
      });
    } else {
      this.catalogo.data(KEY, this.params([PAIS, MODULO, LENGUAJE])).subscribe(response => {
        this.catalogoData.SV[KEY] = response ?
          response.map((p: any) => Object.assign({ value: p.codigo || p.codigoCiiu, label: p.nombre || p.descripcionCiiu }, p))
          : [];
      });
    }
  }

  getCatalogoByFilter(KEY: string, property: string, value: string, pais: string = this.pais, ): void {
    if ((property !== 'ICD' && value) || (value.split(':')[0].length > 0 && value.split(':')[1].length > 0)) {
      this.catalogoData[pais][KEY] = [];
      this.obser.sendData(property, PROPERTY);
      this.catalogo.data(`${KEY}/${value}`, this.params([PAIS, MODULO, LENGUAJE, PROPERTY])).subscribe(response => {
        this.catalogoData[pais][KEY] = response ?
          response.map((p: any) => Object.assign({ value: p.codigo || p.codigoCiiu, label: p.nombre || p.descripcionCiiu }, p))
          : [];
      });
    }
  }

  getCatalogoGeo(
    KEY: string,
    pais: string = this.pais,
    params: any = this.params([PAIS, MODULO, LENGUAJE])
  ): void {
    this.catalogo.data(KEY, params).subscribe(response => {
      this.catalogoData[pais][KEY] = response.map((p: any) => Object.assign({ value: p.codigo, label: p.nombre }, p));
    });
  }

  getCatalogoGeoV2(KEY: string, property?: string, value?: string, pais: string = this.pais, paisResidencia: string = ''): void {
    const params = this.params([PAIS, LENGUAJE]);
    if (property) {
      params.property = property;
    }
    params.modulo = 'CTACAM';
    if (value) {
      const data = value.split(':');
      if (data[1]) {
        if (Number.isNaN(Number(data[1]))) {
          return;
        }
      } else if (Number.isNaN(Number(value))) {
        return;
      }
    }
    if (paisResidencia === 'CO') {
      params.pais = paisResidencia;
    }
    this.catalogo.data(`${KEY}${value ? '/' + value : ''}`, params).subscribe(response => {
       this.catalogoData[pais][KEY] = response.map((p: any) =>
        Object.assign({ value: p.codigo, label: p.nombre.toUpperCase() }));
    });
  }

  esBisiesto(year: string): string {
    if (((parseInt(year, 10) % 4 === 0) && (parseInt(year, 10) % 100 !== 0)) || (parseInt(year, 10) % 400 === 0)) {
      return 'true';
    }
    return 'false';
  }

