  validTransaccionesInternacionales(): ValidatorFn {
    return (control: FormGroup): ValidationErrors | null => {
      const informacionRequerida = control.get('tranferenciasInternacionalesHN').value === false ||
        control.get('operacionesInternacionalesHN').value === false;
      if ( informacionRequerida ) {
        return null;
      }

      const paisOrigenHN = control.get('paisOrigenHN');
      const motivoTransferenciaHN = control.get('motivoTransferenciaHN');
      const nombreEnvioDineroHN = control.get('nombreEnvioDineroHN');

      const paisOrigenHN2 = control.get('paisOrigenHN2');
      const motivoTransferenciaHN2 = control.get('motivoTransferenciaHN2');
      const nombreEnvioDineroHN2 = control.get('nombreEnvioDineroHN2');

      if (
        (!paisOrigenHN.errors && paisOrigenHN.value !== '') &&
        (!motivoTransferenciaHN.errors && motivoTransferenciaHN.value !== '') &&
        (!nombreEnvioDineroHN.errors && nombreEnvioDineroHN.value !== '')
      ) {
        if ( this.paisCatHN.filter( item => item.value === paisOrigenHN.value).length === 1 ) {
          return null;
        } else {
          return {
            paisOrigenHN: true,
            operacionesInternacionales: true
          };
        }
      }

      if (
        !paisOrigenHN2.errors && paisOrigenHN2.value !== '' &&
        !motivoTransferenciaHN2.errors && motivoTransferenciaHN2.value !== '' &&
        !nombreEnvioDineroHN2.errors && nombreEnvioDineroHN2.value !== '' ) {
          if ( this.paisCatHN.filter( item => item.value === paisOrigenHN2.value).length === 1 ) {
            return null;
          } else {
            return {
              paisOrigenHN2: true,
              operacionesInternacionales: true
            };
          }
      }
      return {
        operacionesInternacionales: true
      };
    };
  }

