  // HONDURAS

  validationSelectMonadeExtrangera(campos: Array<string>, error: string, campoValor: string): ValidatorFn {
    const obj = {};
    obj[error] = true;
    return (control: FormGroup): ValidationErrors | null => {
      const valueRequerido = control.get(campoValor).value;
      if (valueRequerido === false) {
        return null;
      }
      let required = true;
      campos = campos.map(item => {
        if (required === true && control.get(item).value === true) {
          required = false;
        }
        return item;
      });
      return required ? obj : null;
    };
  }

