  getTipoOperacionesPA(): void {
    this.tipoOperacionesPA = [];
    this.catalogo.data('TIPO_OPERACION', this.params([PAIS, MODULO, LENGUAJE])).subscribe(response => {
      this.tipoOperacionesPA = response ? response : [];
    });
  }

  getPAisesPA(): void {
    this.paisescatPA = [];
    this.catalogo.data('PAISES', this.params([PAIS, MODULO, LENGUAJE])).subscribe(response => {
      this.paisescatPA = response ?
        response.map((p: any) => Object.assign({ value: p.codigo, label: p.nombre }, p))
        : [];
    });
  }

  getProvinciasPA(KEY: string, property?: string, value?: string, pais: string = this.pais): void {
    const params = this.params([PAIS, LENGUAJE]);
    this.provinciasPA = [];
    if (property) {
      params.property = property;
    }
    params.modulo = 'CTACAM';
    this.catalogo.data(`${KEY}${value ? '/' + value : ''}`, params).subscribe(response => {
      this.provinciasPA = response ? response.map((p: any) =>
        Object.assign({ value: p.codigo, label: p.nombre.toUpperCase() })) : [];
    });
  }

  filtroPA(i): (item) => boolean {
    return (item) => (this.formulario.get('productos') as FormArray).controls[i].get('paisProductoPA').value === item.pais;
  }

  getPayload(): any {
    const payload = this.formulario.getRawValue();
    let retorno = {};
    let objGeneral = {};
    // CostaRica
    if (this.pais === 'CR') {
      const cr = {
        bancoDestino: payload.nombreBancoCR,
        nombreRemitenteDestinatario: payload.nombreRemitentePrincipalCR,
        numeroCuenta: payload.numeroCuentaPrincipalCR,
        paisOrigenDestino: payload.paisDestinoOrigenCR,
        tipoOperacion: payload.tipoOperacionCR
      };
      objGeneral = {
        monto: payload.montoUsdCR,
        operacionesInternacional: payload.operacionesInternacionalesCR === '' ? false : payload.operacionesInternacionalesCR
      };
      retorno = Object.assign(objGeneral, { cr });
    }
    // Honduras
    if (this.pais === 'HN') {
      const hn = {
        compraVentaDivisas: payload.compraVentaDivisasHN,
        emisionGiros: payload.emisionDolaresHN,
        remesasFamiliares: payload.remesasFamiliaresHN,
        transferenciasInternacional: payload.tranferenciasInternacionalesHN,
        montoEventual: payload.montoMensualTipicoHN,
        remesas: {
          motivo: payload.motivoRemesaHN3,
          nombrePersona: payload.nombreRemitenteHN3,
          paisOrigen: payload.paisOrigenHN3,
          relacionRemitente: payload.relacionRemitenteHN3,
        },
        transferenciasEnviada: {
          motivo: payload.motivoTransferenciaHN2,
          nombrePersona: payload.nombreEnvioDineroHN2,
          paisDestino: payload.paisOrigenHN2
        },
        transferenciasRecibida: {
          motivo: payload.motivoTransferenciaHN,
          nombrePersona: payload.nombreEnvioDineroHN,
          paisOrigen: payload.paisOrigenHN
        }
      };
      objGeneral = {
        monto: payload.montoMensualEventualHN,
        operacionesInternacional: payload.operacionesInternacionalesHN === '' ? false : payload.operacionesInternacionalesHN
      };
      retorno = Object.assign(objGeneral, { hn });
    }
    // Panama
    if (this.pais === 'PA') {
      const pa = {
        paisOrigenDestino: payload.paisDestinoPA,
        productosMoneda: payload.productos.map(item => {
          // Solo retornamos los items con información
          if (item.ciudadProductoPA !== '' && item.tipoMonedaOperacionPA !== '') {
            return {
              ciudad: item.ciudadProductoPA,
              nombreBanco: item.nombreBancoPA,
              pais: item.paisProductoPA,
              tipo: item.tipoProductoPA,
              tipoMonedaOperacion: item.tipoMonedaOperacionPA,
            };
          }
        }),
        productosMonedaExtranjera: payload.productosMonedaExtranjeraPA === '' ? false : payload.productosMonedaExtranjeraPA,
        tipoOperacion: payload.tipoOperacionPA
      };
      objGeneral = {
        monto: payload.montoUsdPA,
        operacionesInternacional: payload.operacionesInternacionalesPA === '' ? false : payload.operacionesInternacionalesPA
      };
      // Eliminamos la propiedad productosMoneda si esta vacio el array
      if (pa.productosMoneda.length === 0 || pa.productosMoneda[0] === undefined) {
        delete pa.productosMoneda;
      }
      retorno = Object.assign(objGeneral, { pa });
    }
    // El Salvador
    if (this.pais === 'SV') {
      console.log('payload.productos', payload.productos);
      const sv = {
        bancoDestino: payload.nombreBancoDestinoSV,
        nombreOtraOperacion: payload.cual,
        nombreRemitenteDestinatario: payload.nombreRemitenteSV,
        numeroCuenta: payload.numeroCuentaPrincipalSV,
        otraOperacionesInternacional: payload.otroTipoOperacion === '' ? false : payload.otroTipoOperacion,
        paisDestino: payload.paisDestinoSV,
        paisOrigen: payload.paisOrigenSV,
        productosMoneda: payload.productos.map(item => {
          // Solo retornamos los items con información
          if (item.ciudadProductoSV !== '' && item.numeroProductoSV !== '') {
            return {
              ciudad: item.ciudadProductoSV,
              monto: item.montoMensualUsdSV,
              nombreBanco: item.nombreBancoProductoSV,
              numero: item.numeroProductoSV,
              pais: item.paisProductoSV,
              tipo: item.tipoProductoSV2,
              tipoMonedaOperacion: item.tipoMoendaOperacionSV
            };
          }
        }),
        productosMonedaExtranjera: payload.productosMonedaExtranjeraSV2 === '' ? false : payload.productosMonedaExtranjeraSV2,
        tipoMonedaOperacion: payload.tipoMonedaOperacionSV,
        tipoOperacion: payload.tipoOperacionSV
      };
      objGeneral = {
        monto: payload.montoUsdSV,
        operacionesInternacional: payload.operacionesInternacionalesSV === '' ? false : payload.operacionesInternacionalesSV
      };
      // Si no tiene productosMoneda, eliminamos la propiedad
      if (sv.productosMoneda.length === 0 || sv.productosMoneda[0] === undefined) {
        delete sv.productosMoneda;
      }
      retorno = Object.assign(objGeneral, { sv });
    }

    return retorno;
  }
