  ngOnInit(): void {
    // Setear la fecha de nacimiento, campo común para todos los paises
    this.dia = '00';
    this.mes = '00';
    this.anio = '0000';
    if ( this.payload && this.hasProperty(this.payload, 'fechaNacimiento') &&
      this.payload.fechaNacimiento.split('/').length === 3 ) {
      const d = this.payload.fechaNacimiento.split('/');
      this.dia = new Date(d[2], d[1] - 1, d[0]).getDate() + '';
      this.dia = this.dia.length === 1 ? '0' + this.dia : this.dia;
      this.mes = (new Date(d[2], d[1] - 1, d[0]).getMonth() + 1) + '';
      this.mes = this.mes.length === 1 ? '0' + this.mes : this.mes;
      this.anio = new Date(d[2], d[1] - 1, d[0]).getFullYear() + '';
    }

    // Setear la fecha de vencimiento
    this.diaVen = '';
    this.mesVen = '';
    this.anioVen = '';
    if (this.payload && this.hasProperty(this.payload, 'fechaVencimiento') &&
      this.payload.expedicion.fechaVencimiento.split('/').length === 3) {
      const d = this.payload.expedicion.fechaVencimiento.split('/');
      this.diaVen = new Date(d[2], d[1] - 1, d[0]).getDate() + '';
      this.diaVen = this.diaVen.length === 1 ? '0' + this.diaVen : this.diaVen;
      this.mesVen = (new Date(d[2], d[1] - 1, d[0]).getMonth() + 1) + '';
      this.mesVen = this.mesVen.length === 1 ? '0' + this.mesVen : this.mesVen;
      this.anioVen = new Date(d[2], d[1] - 1, d[0]).getFullYear() + '';
    }

    // Setear la fecha de expedicion
    this.diaExp = '';
    this.mesExp = '';
    this.anioExp = '';
    if (this.payload && this.hasProperty(this.payload, 'fechaExpedicion') &&
      this.payload.expedicion.fechaExpedicion.split('/').length === 3) {
      const d = this.payload.expedicion.fechaExpedicion.split('/');
      this.diaExp = new Date(d[2], d[1] - 1, d[0]).getDate() + '';
      this.diaExp = this.diaExp.length === 1 ? '0' + this.diaExp : this.diaExp;
      this.mesExp = (new Date(d[2], d[1] - 1, d[0]).getMonth() + 1) + '';
      this.mesExp = this.mesExp.length === 1 ? '0' + this.mesExp : this.mesExp;
      this.anioExp = new Date(d[2], d[1] - 1, d[0]).getFullYear() + '';
    }

    this.country = [];
    this.countrieRules = {
      CR: () => this.setRulesCR(),
      PA: () => this.setRulesPA(),
      SV: () => this.setRulesSV(),
      HN: () => this.setRulesHN()
    };
    if (this.countrieRules[this.pais]) {
      this.countrieRules[this.pais]();
    }
  }

