  getPaises(IdPais: string): void {
    // this.paisesCR = [];
    this.paises = [];
    this.catalogo.data('PAISES', this.params([PAIS, MODULO, LENGUAJE])).subscribe(response => {
      this.paises = response ?
        response.map((p: any) => Object.assign({ value: p.codigo, label: p.nombre }, p))
        : [];
      this.reorderContry(this.paises, IdPais);
    });
  }

  getLugarExpedicion(): void {
    this.catalogoData.SV.LUGAR_EXPEDICION = [];
    this.catalogo.data('LUGAR_EXPEDICION', this.params([PAIS, MODULO, LENGUAJE])).subscribe(response => {
      this.catalogoData.SV.LUGAR_EXPEDICION  = response ?
        response.map((p: any) => Object.assign({ value: p.codigo, label: p.nombre }, p))
        : [];
    });
  }

  reorderContry(paises: Array<{ value: string, label: string; }>, idPais: string): void {
    let index = 0;
    let pais: any;
    index = paises.indexOf(paises.filter(item => item.value === idPais)[0]);
    pais = paises.filter(item => item.value === idPais)[0];
    paises.splice(index, 1);
    if ( pais ) {
      paises.splice(0, 0, pais);
      return;
    }
  }

