  getProvinciasCR(KEY: string, property?: string, value?: string, pais: string = this.pais): void {
    this.provinciasCR = [];
    const params = this.params([PAIS, LENGUAJE]);
    if (property) {
      params.property = property;
    }
    if (value) {
      const data = value.split(':');
      if (data[1]) {
        if (Number.isNaN(Number(data[1]))) {
          return;
        }
      } else if (Number.isNaN(Number(value))) {
        return;
      }
    }
    params.modulo = 'CTACAM';
    this.catalogo.data(`${KEY}${value ? '/' + value : ''}`, params).subscribe(response => {
      if (response) {
        this.provinciasCR = response.map((p: any) =>
          Object.assign({ value: p.codigo, label: p.nombre.toUpperCase() }));
      }
    });
  }



