  // PANAMA

  onChangePaisResidenciaPA(value: string): void {
    if (value === '') {
      return;
    }

    this.catalogoData.PA.catCiudades = [];
    this.catalogoData.PA.catDepartamentos = [];

    if (value === 'CO') {
      this.formulario.get('corregimiento').setValue('');
      this.getCatalogoGeoV2(zonaGeograficaNivel1, null, null, this.pais, value);
    } else if (value === 'PA') {
      this.catalogoData.PA.catMunicipios = [];
      this.getCatalogoGeoV2(zonaGeograficaNivel1);
    } else {
      this.formulario.get('corregimiento').setValue('');
      this.formulario.get('distrito').setValue('');
      this.formulario.get('provincia').setValue('');
    }
  }

