  getCatalogoByFilter(KEY: string, property: string, value: string, pais: string = this.pais, ): void {
    if ((property !== 'ICD' && value) || (value.split(':')[0].length > 0 && value.split(':')[1].length > 0)) {
      this.catalogoData[pais][KEY] = [];
      this.obser.sendData(property, PROPERTY);
      this.catalogo.data(`${KEY}/${value}`, this.params([PAIS, MODULO, LENGUAJE, PROPERTY])).subscribe(response => {
        this.catalogoData[pais][KEY] = response ?
          response.map((p: any) => Object.assign({ value: p.codigo, label: p.nombre }, p))
          : [];
      });
    }
  }

  getCatalogoGeoV2(KEY: string, property?: string, value?: string, pais: string = this.pais, paisResidencia: string = ''): void {
    const params = this.params([PAIS, LENGUAJE]);
    if (property) {
      params.property = property;
    }
    if (value) {
      const data = value.split(':');
      if (data[1]) {
        if (Number.isNaN(Number(data[1]))) {
          return;
        }
      } else if (Number.isNaN(Number(value))) {
        return;
      }
    }
    params.modulo = 'CTACAM';
    if (paisResidencia === 'CO') {
      params.pais = paisResidencia;
    }
    const SUBSCRIPTION = this.catalogo.data(`${KEY}${value ? '/' + value : ''}`, params).subscribe(response => {
      if (response) {
        this.catalogoData[pais][KEY] = response.map((p: any) =>
          Object.assign({ value: p.codigo, label: p.nombre.toUpperCase() }));
        setTimeout(() => { SUBSCRIPTION.unsubscribe(); }, 300);
      }
    });
  }

  getPayload(): any {
    const payload = this.formulario.getRawValue();
    const numPrefijo = PREFIX_NUMBER[this.pais];

    let retorno = {
      correo: payload.correo,
      estadoCivil: payload.estadoCivil,
      fechaNacimiento: payload.fechaNacimiento,
      noCelular: payload.noCelular,
      indicativoCel: numPrefijo,
      paisNacimiento: payload.paisNacimiento
    };

    // CostaRica
    if (this.pais === 'CR') {
      const cr = {
        canton: payload.corregimiento,
        direccionExacta: payload.direccionExacta,
        distrito: payload.distrito,
        esPrincipal: payload.esPrincipal === '' ? false : payload.esPrincipal,
        especifiqueNacionalidad: payload.otraNacionalidad ? payload.especifiqueNacionalidad : '',
        genero: payload.genero,
        fechaVencimiento: {
          anio: payload.fechaVencimiento.anio,
          dia: payload.fechaVencimiento.dia,
          mes: payload.fechaVencimiento.mes
        },
        nacionalidad: payload.nacionalidad,
        otraNacionalidad: payload.otraNacionalidad,
        provincia: payload.provincia
      };

      retorno = Object.assign(retorno, { cr });
    }
    // Honduras
    if (this.pais === 'HN') {
      const hn = {
        autorizoSMS: payload.autorizoSMS,
        barrioColonia: payload.barrioColonia,
        colonia: payload.colonia,
        conyuge: payload.conyuge,
        departamento: payload.departamento,
        detalle: payload.detalle,
        genero: payload.genero,
        esDireccionPrincipal: payload.esDireccionPrincipal,
        municipio: payload.municipio,
        nacionalidad: payload.nacionalidad,
        noTelefono: payload.noTelefono,
        indicativoTel: numPrefijo,
        referencia: payload.referenica,
      };
      retorno = Object.assign(retorno, { hn });
    }

    // Panama
    if (this.pais === 'PA') {
      const pa = {
        corregimiento: payload.corregimiento,
        detalleDireccion: payload.detalleDireccion,
        distrito: ['CO', 'PA'].includes(payload.paisResidencia) ? payload.distrito : payload.ciudad,
        esPrincipal: payload.esPrincipal,
        fechaExpedicion: {
          anio: payload.fechaExpedicion.anio,
          dia: payload.fechaExpedicion.dia,
          mes: payload.fechaExpedicion.mes
        },
        fechaVencimiento: {
          anio: payload.fechaVencimiento.anio,
          dia: payload.fechaVencimiento.dia,
          mes: payload.fechaVencimiento.mes
        },
        lugarExpedicion: payload.lugarExpedicion,
        otraNacionalidad: payload.otraNacionalidad === '' ? false : payload.otraNacionalidad,
        otroPaisNacimiento: payload.otroPaisNacimiento,
        paisResidencia: payload.paisResidencia,
        provincia: payload.provincia,
        tipoVivienda: payload.tipoVivienda,
      };
      retorno = Object.assign(retorno, { pa });
    }

    // El Salvador
    if (this.pais === 'SV') {
      const sv = {
        direccionExacta: payload.direccionExacta,
        esPrincipal: payload.esPrincipal,
        fechaExpedicionDUI: {
          anio: payload.fechaExpedicionDUI.anio,
          dia: payload.fechaExpedicionDUI.dia,
          mes: payload.fechaExpedicionDUI.mes
        },
        fechaVencimientoDUI: {
          anio: payload.fechaVencimientoDUI.anio,
          dia: payload.fechaVencimientoDUI.dia,
          mes: payload.fechaVencimientoDUI.mes
        },
        lugarExpedicion: payload.lugarExpedicion,
        nacionalidad: payload.nacionalidad,
        nombreConyugue: payload.nombreConyugue,
        numeroDependientes: payload.numeroDependientes,
        paisDepartamento: payload.paisResidencia === 'SV' ? payload.paisDepartamento : '',
        paisMunicipio: payload.paisResidencia === 'SV' ? payload.paisMunicipio : '',
        paisResidencia: payload.paisResidencia,
        nit: payload.numeroNITSV
      };
      retorno = Object.assign(retorno, { sv });
    }
    return retorno;
  }

   homologarEstadoTicNow(codigoPais: any, codigoEstado: any): string {
    switch (codigoPais) {
      case 'CR':
        switch (codigoEstado) {
          case 'SO':
            return 'S';
          case 'CA':
            return 'C';
          case 'DI':
            return 'D';
          case 'SE':
            return 'L';
          case 'UL':
            return 'L';
          case 'VI':
            return 'V';
          default:
            return codigoEstado;
        }
      case 'HN':
        switch (codigoEstado) {
          case 'SO':
            return 'S';
          case 'CA':
            return 'C';
          case 'DI':
            return 'D';
          case 'SE':
            return 'L';
          case 'UL':
            return 'L';
          case 'VI':
            return 'V';
          default:
            return codigoEstado;
        }
      case 'PA':
        switch (codigoEstado) {
          case 'SO':
            return '1';
          case 'CA':
            return '2';
          case 'DI':
            return '3';
          case 'VI':
            return '4';
          default:
            return codigoEstado;
        }
      case 'SV':
        switch (codigoEstado) {
          case 'SO':
            return '1';
          case 'CA':
            return '2';
          case 'DI':
            return '3';
          case 'VI':
            return '4';
          case 'AC':
            return '5';
          default:
            return codigoEstado;
        }
      default:
        return codigoEstado;
    }
  }

  mascaraNIT(simbol: string): (value: string) => string {
    return (value) => {
      const primerbloque = value.slice(0, 4);
      const segundobloque = value.slice(4, 10);
      const tercerbloque = value.slice(10, 13);
      const cuartobloque = value.slice(13, 14);

      const total = primerbloque + '-' + segundobloque + '-' + tercerbloque + '-' + cuartobloque;
      return total;
    };
  }
