  /*
    Funcion general para validar
    un control del formulario.
  */
  validationControl(controlName: string) {
    return (control: FormGroup) => {
      if (!control.parent) {
        return true;
      }
      if (control.parent.get(controlName).value === true) {
        return control.value !== '';
      }
      return true;
    };
  }

  validationRequiredOne(campos: Array<string>, error: string): ValidatorFn {
    const obj = {};
    obj[error] = true;
    return (control: FormGroup): ValidationErrors | null => {
      if (this.showOptionsHN === false) {
        return null;
      }
      let required = true;
      campos = campos.map(item => {
        if (required === true && control.get(item).value === true) {
          required = false;
        }
        return item;
      });
      return required ? obj : null;
    };
  }

