  /*
    Suma los días de estadía en USA y valida si
    es mayor al numero indicado.
  */
  sumDays() {
    /* Numero de dias permitidos. */
    const days = 183;

    const diasAnioCurso = this.isNullOrEmpty(this.FC.diasAnioCursoHN.value) ? 0 : this.FC.diasAnioCursoHN.value;
    const diasAnioPasado = this.isNullOrEmpty(this.FC.diasAnioPasadoHN.value) ? 0 : this.FC.diasAnioCursoHN.value;
    const diasAnioAntepasado = this.isNullOrEmpty(this.FC.diasAnioAntepasadoHN.value) ? 0 : this.FC.diasAnioCursoHN.value;

    if ((!Number.isInteger(+diasAnioCurso))
      || (!Number.isInteger(+diasAnioPasado))
      || (!Number.isInteger(+diasAnioAntepasado))) {
      this.showOptionsHN = false;
      return;
    }
    /*
      Se suma el 100% de los dias del año en curso
      1/3 de los dias del año pasado y
      1/6 de los dias del año antepasado.
    */
    const sum = (+diasAnioCurso) + ((+diasAnioPasado) / 3) + ((+diasAnioAntepasado) / 6);
    this.showOptionsHN = sum >= days ? true : false;
  }

  /*
    Validaciones para ocultar o mostrar
    campos en el HTML de Honduras.
  */
  getConditionHN(controlName: string): boolean {
    const FC = this.formulario.controls;
    const nacionalidadHN = FC.ciudadaniaUsaHN.value === true;
    const nacionalidadUsaHN = nacionalidadHN && FC.nacionalidadHN.value !== 'US';
    const pasaporteUsaHN = nacionalidadUsaHN && FC.nacionalidadUsaHN.value === false;
    const otraResidenciaHN = pasaporteUsaHN && FC.pasaporteUsaHN.value === false;
    const residenciaHN = otraResidenciaHN && FC.otraResidenciaHN.value !== 'US';
    const tiempoEstadiaHN = residenciaHN && FC.residenciaHN.value === false;

    const numeroPasaporteHN = pasaporteUsaHN && FC.pasaporteUsaHN.value === true;
    const numeroTinHN = nacionalidadHN && ((FC.nacionalidadHN.value === 'US') || (FC.nacionalidadUsaHN.value === true)
      || (FC.pasaporteUsaHN.value === true) || (FC.otraResidenciaHN.value === 'US') || (FC.residenciaHN.value === true));

    return {
      nacionalidadHN,
      nacionalidadUsaHN,
      pasaporteUsaHN,
      otraResidenciaHN,
      residenciaHN,
      numeroPasaporteHN,
      numeroTinHN,
      tiempoEstadiaHN
    }[controlName];
  }

  isNullOrEmpty(value?: any) {
    return value === null || value === '';
  }
