import { Component, OnInit, EventEmitter, Input, Output, forwardRef } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';

export const INPUT_PHONE_CONTROL_VALUE_ACCESOR = {
  provide: NG_VALUE_ACCESSOR,
  // tslint:disable-next-line: no-use-before-declare
  useExisting: forwardRef(() => InputPhoneComponent),
  multi: true
};

@Component({
  selector: 'app-input-phone',
  templateUrl: './input-phone.component.html',
  styleUrls: ['./input-phone.component.scss'],
  providers: [INPUT_PHONE_CONTROL_VALUE_ACCESOR]
})

export class InputPhoneComponent implements OnInit, ControlValueAccessor {
  // tslint:disable-next-line: no-input-rename variable-name
  @Input('value') _value = '';
  @Input() label = '';
  @Input() infoSpan = '';
  @Input() form: any;
  @Input() formControlName: string;
  @Input() restrict: string;
  @Input() maxLength: string;

  onChange: any = () => {};
  onTouched: any = () => {};


  constructor() { }

  ngOnInit() {
  }

  get value() {
    return this._value;
  }

  set value(value) {
    this._value = value;
    this.onChange(value);
    this.onTouched();
  }

  writeValue(value) {
    this._value = value;
  }

  registerOnTouched(fn) {
    this.onTouched = fn;
  }

  registerOnChange(fn) {
    this.onChange = fn;
  }

}
